.profile-setup-container {
  max-width: 550px;
  overflow: hidden;
}

.setup-step {
  text-align: center;
  flex-direction: column;
  align-items: center;
  animation: .5s ease-in-out fadeIn;
  display: none;
}

.setup-step.active {
  display: flex;
}

.setup-step h1 {
  margin-bottom: .5rem;
  font-size: 1.8rem;
}

.setup-step p {
  color: var(--light-text-color);
  margin-bottom: 2rem;
}

.setup-step form {
  width: 100%;
}

.preferences-grid {
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  width: 100%;
  margin-bottom: 2rem;
  display: grid;
}

.preference-card {
  border: 2px solid var(--border-color);
  cursor: pointer;
  background-color: #f9f9f9;
  border-radius: 15px;
  flex-direction: column;
  align-items: center;
  gap: .5rem;
  padding: 1.5rem;
  transition: all .3s;
  display: flex;
}

.preference-card i {
  color: var(--light-text-color);
  font-size: 2.5rem;
  transition: all .3s;
}

.preference-card span {
  font-weight: 600;
  transition: all .3s;
}

.preference-card:hover {
  border-color: var(--primary-color);
  transform: translateY(-5px);
}

.preference-card.selected {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
  color: var(--white-color);
  transform: scale(1.05);
}

.preference-card.selected i, .preference-card.selected span {
  color: var(--white-color);
}

.social-input {
  width: 100%;
  margin-bottom: 1rem;
  position: relative;
}

.social-input i {
  color: var(--light-text-color);
  position: absolute;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
}

.social-input input {
  padding-left: 40px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
